import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';
import './Portfolio.css';



class Portfolio extends Component {
  render() {
    const { t, style, portfolioOngoing } = this.props;
    return (
      <div className="portfolio-ongoing">
        <div className="portfolio-container">
          {(portfolioOngoing.backgroundLarge || portfolioOngoing.background) ? (
            <img className="portfolio-background" src={portfolioOngoing.backgroundLarge ? portfolioOngoing.backgroundLarge : portfolioOngoing.background} alt={portfolioOngoing.title} />
          ) : ""}

          <div className="logoContainer" id="001">
            <img className="marco2" src={portfolioOngoing.marcoDesktop ? portfolioOngoing.marcoDesktop : portfolioOngoing.marco} alt="" />
            <img className="logo2" src={portfolioOngoing.icon} alt={portfolioOngoing.title} />
            {portfolioOngoing.background ? (
              <img className="background-mobile" src={portfolioOngoing.background} alt={portfolioOngoing.title} />
            ) : ""}
            <img className="marco" src={portfolioOngoing.marco} alt="" />
            <li className= "backgroundLogoContainer"> 
              <img className="logo" src={portfolioOngoing.icon} alt={portfolioOngoing.title} />
            </li>
            {(portfolioOngoing.iconSmall) ? (
              <>
                <a className= "backgroundLogoContainer" href={portfolioOngoing.link} rel='noreferrer noopener' target="_blank" style={{ display: "block" }}>
                  <img className="logo3" src={portfolioOngoing.iconSmall} alt={portfolioOngoing.title} />
                </a>
                <p style={{ color: style.link }} className="txtLink">{t(this.props.portfolioOngoing.txtLink)}</p>
              </>
            ) : ""}
          </div>
          <div className="textContainer">
            <p style={{ color: style.title }} className="title">
              {t(portfolioOngoing.title)}
            </p>
            <p className="description-text"> {t(portfolioOngoing.description)}</p>
          {(portfolioOngoing.link) ? (
            <a
              style={{ color: style.link }}
              href={portfolioOngoing.link}
              target="_blank" // see https://github.com/yannickcr/eslint-plugin-react/blob/master/docs/rules/jsx-no-target-blank.md
              rel='noreferrer noopener'
              className="link"> {portfolioOngoing.linkTitle}
            </a>
          ) : ""}
          </div>
        </div>
      </div >
    );
  }
}

export default withTranslation()(Portfolio);
