import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import './Description.css';



class DescriptionClass extends Component {
  render() {

    const { t, i18n } = this.props;

    return (
      <div className="description">
        <div className="unit-container">
          <p style={{color: this.props.unity.style.general}} className="description-text"> {t(this.props.unity.description)}</p>
          <img className="icon" src={this.props.unity.icon} alt={this.props.unity.name}/>
        </div>
      </div>
    );
  }
}

const UnityDescription = withTranslation()(DescriptionClass);

export default UnityDescription;