import React, { Component } from 'react';
import { withTranslation} from 'react-i18next';
import './Hero.css';


class Hero extends Component {
  render() {
    const { t,  hero } = this.props;

    return (
      <div className="Hero">
        <div className="Hero-mobile-container">
          <img className="Hero-marco" src={this.props.marcoMobile} alt="marcoHero" />
          <img className="Hero-marco-desktop" src={this.props.marco1Desktop} alt="marcoHero" />
          <img className="Hero-marco2-desktop" src={this.props.marco2Desktop} alt="marcoHero" />
          <img className="Hero-lineas" src={this.props.lineas} alt="marcoHero" />
          <a href={hero.url} target={"_blank"}>
            <img className="Hero-photo" src={hero.photo} alt="photoHero" />
          </a>  
          <p className="Hero-name">{hero.name}          
            <img className="Hero-marco-text" src={this.props.marcoText} alt="marcoTextHero" />
          </p>
        </div>
          <p className="Hero-description">{t(hero.description)}</p>
      </div>
    );
  }
}

export default withTranslation()(Hero);
