import React, { Component } from 'react';
import './Client.css';



class Client extends Component {
  render() {
    return (
      <div className="Client">
        <div className="Client-container">
          <div className="Client-marcoContainer">
            <p style={{ color: this.props.color }} className="Client-title">
              {this.props.client.title}
              <img className="Client-title-marco" src="/img/Soft/Marco.png" alt=""></img>
            </p>
              <a href={this.props.client.link} target="_blank"><img className="Client-logo" src={this.props.client.icon} alt={this.props.client.title}></img></a>
          </div>
        </div>
      </div>
    );
  }
}

export default Client;
